.headshot {
  width: 80%;
  margin: auto;
  height: auto;
  border-radius: 50%;
  cursor: pointer;

  -webkit-animation: fadein 3s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
        animation: fadein 3s;
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Internet Explorer */

/* Opera < 12.1 */

@media screen and (max-width: 887px) {
  .headshot-wrapper {
    max-width: 100%;
    margin: auto;
  }
}



