.button {
  text-decoration: none;
  padding: 8px;
  font-size: 20px;
  border: 2px solid #FF5008;
  border-radius: 8px;
  background-color: #FF5008;
  -webkit-transition: 0.25s ease;
  -o-transition: 0.25s ease;
  transition: 0.25s ease;
  color: white;
}

.button:hover {
  color: #FF5008;
  background-color: white;
}

.project-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 180px;
  -ms-flex-align: center;
      align-items: center;
  margin: auto;
  margin-bottom: 15px;
}

#four .live-button, #four .repo-button {
  font-size: 15px;
}



